            //         left = scroll.x - pp.left;
            //     }

            //     if (pp.left + left + this.lensSize.width > scroll.x + ws.width) {
            //         left = scroll.x - pp.left + ws.width - this.lensSize.width;
            //     }
            // }

            this.lensPosition = { top: top, left: left };
